export const kilina2012Abstract = 
`Using time-dependent density functional theory, we found that chemical functionalization at low concentrations of single-walled carbon nanotubes (SWNTs) locally alters the -conjugated network of the nanotube surface and leads to a spatial confinement of the electronically excited wave functions. Depending on the adsorbant positions, the chemisorption significantly modifies the optical selection rules. Our modeling suggests that photoluminescent efficiency of semiconducting SWNT materials can be controlled by selective chemical functionalization.`;